import { StorageWebService } from './../../Services/Storage/StorageWeb.service';
import { UserRegister } from './../../Interfaces/user.interface';
import { Component, OnInit } from '@angular/core';
import { ApiService } from 'src/app/Services/Api/Api.service';
import { trigger, transition, query, style, stagger, animate } from '@angular/animations';

import { PlatformsService } from 'src/app/Services/PLatforms/Platform.service';
import { Router } from '@angular/router';
import { MenuController } from '@ionic/angular';
import { Socket } from 'ngx-socket-io';

@Component({
  selector: 'app-central',
  templateUrl: './central.page.html',
  animations: [
    trigger('welcome', [
      transition('* => *', [
        query(':enter', [
          style({
            opacity: 0,
             transform: 'translateX(-100px)'
            }),
          stagger(100, [
            animate('300ms',
            style({
               opacity: 1,
                transform: 'none'
              }))
          ])
        ], {optional: true})
      ])
    ])
  ],
  styleUrls: ['./central.page.scss'],
})
export class CentralPage implements OnInit {

  dataUser: any = {
    AccesToken: '90BA1FD909F45177050A18D22AF00BA0'
  };

  listForms = [];
  draw = false;
  loadForms = false;
  plt = '';
  constructor(
    private apiService: ApiService,
    private myPlatform: PlatformsService,
    private storageService: StorageWebService,
    private router: Router,
    private menuCtrl: MenuController,
    private socket: Socket
  ) { 

    this.myPlatform.$myplatform.subscribe((plt: any) => {
      this.plt = plt;
    })
  }

  ngOnInit() {

  }


  async ionViewWillEnter() {
     this.listForms = [];
     if (this.listForms.length === 0) {
      this.loadForms = true;
     }
     await this.menuCtrl.enable(true, 'menu');
    // this.sqliteService.getToken().then((token) => {
       // console.log(token, 'Token');
     this.dataUser.AccessToken = await this.storageService.getToken();
    //  console.log(this.dataUser);
     this.apiService.getForm(this.dataUser, {}).subscribe((data: any) => {
          // console.log(JSON.parse(data));
          data = JSON.parse(data);
          this.loadForms = false;
          
          data.forEach(element => {
            if (element.isDeleted !== 'True') {
             this.listForms.push(element);
            }
          });
          if (this.plt === 'web') {
            this.storageService.insertForms(data);
          }

          console.log(this.listForms);
        });

    
    /* }).catch((err) => {
       console.log(err);
     })*/
     

  }

  async clickForm(form: any) {
    try {
      console.log(form);
      form.JSONQuestions = JSON.parse(form.JSONQuestions);
      const d = await this.storageService.insertFormActive(form);

      if (d) {
        this.router.navigate(['/form']);
      }

    } catch (error) {
      
    }
  }
}
